.APIAttachment>h1 {
    font-size: 2em;
    font-weight: 300;
    margin: 20px;
    color: #ccc;
}

.APIAttachment {
    max-height: 50vh;
    overflow-y: scroll;
    width: 100%;
    display: none;
}


.APIAttachment>.appendingAttachmentDiv>div {
    display: grid;
    gap: 1.5em;
    /* width: 90%; */
    grid-template-columns: 20px auto auto 20px 20px;
}

.APIAttachment>.appendingAttachmentDiv>div>input,
.APIAttachment>.appendingAttachmentDiv>div>button {
    background-color: transparent;
    color: #ccc;
    border: none;
    font-size: 1.2em;
    border-bottom: 1px solid #ccc;
    opacity: 0.6;
    padding: 10px;
    outline: none;
}

.APIAttachment>.appendingAttachmentDiv>textarea {
    width: 90%;
    background-color: #282828;
    border: 1px solid #ccc;
    border-radius: 10px;
    color: #ccc;
    padding: 10px;
    font-size: 1.2em;
    outline: none;
    height: 30vh;
    size-adjust: none;
}

.APIAttachment>.appendingAttachmentDiv>div>button {
    border: none;
    display: none;
}

.APIAttachment>.appendingAttachmentDiv>div:hover button {
    display: block;
}

.bodyAttachmentSection {
    display: none;
    width: 100%;
    max-height: 50vh;
}



.bodyAttachmentSection>.bodyOptions {
    display: flex;
    gap: 2em;
    width: 100%;
    margin: 20px;
    --bottom: -7px;
    --height: 3px;
}



.bodyAttachmentSection>.bodyOptions>button {
    background-color: transparent;
    color: #ccc;
    border: none;
    font-size: 1.2em;
    font-weight: 300;
    position: relative;
}