.requestList {
    background-color: #333;
    border-radius: 0px 3em 3em 0px;
    box-sizing: border-box;
    height: 100svh;
    overflow: hidden;
}

.underLineHeaiding {
    margin: 50px 30px;
    font-weight: 600;
    font-size: 2.2em;
    position: relative;
    width: fit-content;
}

.underLineHeaiding::after {
    content: "";
    position: absolute;
    width: 120%;
    height: 5px;
    border-radius: 10px;
    background-color: var(--theme-color);
    bottom: -15px;
    left: 0px;
}


.underLineHeaiding::before {
    content: "";
    position: absolute;
    border: 10px solid transparent;
    border-left: 20px solid var(--theme-color);
    bottom: -23px;
    right: -30%;
}

.requestList>.newRequestAddBtn {
    width: 80%;
    display: block;
    margin: auto;
    padding: 10px;
    background-color: #444;
    border: none;
    font-size: 1.2em;
    color: #ccc;
    border-radius: 10px;
    margin-bottom: 50px;
}

.requestList>.requestBtnsList {
    height: 70svh;
    overflow-y: scroll;
}

.requestList>.requestBtnsList>.requestBtn {
    width: 100%;
    box-sizing: border-box;
    display: flex;
    background-color: #333;
    border: none;
    color: #ccc;
    justify-content: space-between;
    padding: 10px 20px;
    /* border: 1px solid red; */
    align-items: center;
}

.requestList>.requestBtnsList>.requestBtn>.requestMethod {
    background-color: lime;
    color: #000;
    padding: 5px;
    border-radius: 20px;

}

.requestList>.requestBtnsList>.requestBtn>.detail {
    width: 60%;
    font-size: 1.2em;

}

.requestList>.requestBtnsList>.requestBtn>.detail>p:last-child {
    font-size: 0.5em !important;
}

.requestList>.requestBtnsList>.requestBtn>.editOptionBtn {
    background-color: transparent;
    border: none;
    padding: 10px;
    font-size: 1.3em;
    color: #ccc;
    position: relative;
}

/* hover */

.requestList>.newRequestAddBtn:hover {
    background-color: #555;
}

.requestList>.requestBtnsList>.requestBtn:hover {
    background-color: #282828;
}

.selectedRequest {
    background-color: #181818 !important;
}

.editOptionBtn>.editMenu {
    position: absolute;
    /* display: none; */
    right: 0;
    background-color: #232323;
    padding: 10px;
    display: grid;
    z-index: 1;
    gap: 1em;
    scale: 0;
    transition: 0.3s ease-in-out;
    transform-origin: top right;
    border-radius: 10px;
}

.editOptionBtn>.editMenu>button {
    background-color: transparent;
    border: none;
    color: #ccc;
    font-size: 1.1em;
    padding: 5px 10px;
    border-radius: 10px;
}

.editOptionBtn>.editMenu>button>i {
    pointer-events: none;
}

.editOptionBtn>.editMenu>button:hover {
    background-color: #ccc;
    color: #282828;
}

.showMenu>.editMenu {
    scale: 1 !important;
}