.responseSection {
    display: none;
    width: 100%;
}

.responseSection>.responseStatus {
    display: grid;
    grid-template-columns: repeat(3, auto);
    margin: 30px;
}

.responseSection>.responseStatus>p {
    text-align: center;
}

.responseSection>.responseStatus>p>span {
    color: lime;
    margin: 10px;
}

.ResponseDiv {
    position: relative;
}

.ResponseDiv>textarea {
    width: 80%;
    resize: none;
    background-color: #333;
    border: none;
    border-radius: 10px;
    padding: 10px;
    font-size: 1.2em;
    color: #ccc;
    outline: none;
    height: 50vh;
    display: block;
    margin: auto;
}

.ResponseDiv>.loadingScreen {
    display: none;

    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    font-size: 5em;
    color: var(--theme-color, aqua);
    align-items: center;
    justify-content: center;
    background-color: #111;

}

.ResponseDiv>.loadingScreen>i {
    animation: rotate 2s linear infinite;
}

@keyframes rotate {
    0% {
        rotate: 0deg;
    }

    100% {
        rotate: 360deg;
    }

}