.mainSection {
    height: 100svh;
    padding: 0px 10%;
    padding-top: 100px;
    box-sizing: border-box;
}

.mainSection>.apiInputSection {
    background-color: #232323;
    border-radius: 10px;
    display: grid;
    grid-template-columns: 1fr 4fr 1fr;
    overflow: hidden;
    border: 1px solid #444;
}


.mainSection>.apiInputSection>#selectMethod,
.mainSection>.apiInputSection>input,
.mainSection>.apiInputSection>.sendRequestBtn {
    padding: 15px;
    background-color: inherit;
    border: none;
    color: #ccc;
    outline: none;
    border: 1px solid #444;
    font-size: 1.2em;


}

.mainSection>.apiInputSection>#selectMethod {
    border-radius: 10px 0px 0px 10px;
}

.mainSection>.apiInputSection>.sendRequestBtn {
    border-radius: 0px 10px 10px 0px;
    background-color: var(--theme-color);
    color: #000;
    font-weight: 600;
}

.mainSection>.apiInputSection>.sendRequestBtn:hover {
    opacity: 0.9;
}

.mainSection>.requestAttachmentOptions {
    margin-top: 50px;
    display: flex;
    gap: 4em;
    padding: 20px;
    border-bottom: 1px solid #ccc;
}


.mainSection>.requestAttachmentOptions>button {
    font-size: 2em;
    background-color: transparent;
    border: none;
    color: #ccc;
    position: relative;
    font-weight: 300;
}

.activeOption {
    font-weight: 600 !important;
}

.mainSection .activeOption::after {
    content: "";
    position: absolute;
    width: 100%;
    height: var(--height, 5px);
    background-color: var(--theme-color);
    left: 0px;
    bottom: var(--bottom, -22px);
    border-radius: 10px;
}