* {
  margin: 0;
  padding: 0;
  /* border: 1px solid red; */
}

:root {
  --theme-color: #5BB6FF;
}

::-webkit-scrollbar {
  width: 2px;
}

::-webkit-scrollbar-thumb {
  background-color: var(--theme-color, aqua);
}

body {
  background-color: #181818;
  color: #fff;
}

.App {
  display: grid;
  grid-template-columns: 1fr 3fr;
  height: 100svh;
  width: 100vw;
}